/*--spoilers-*/
/*--------------------adjustment----------------------*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  font-family: "MontRegular", sans-serif;
  font-weight: normal;
  font-size: 15px;
  min-width: 320px;
  position: relative;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  color: #000;
}
.lock body {
  overflow: hidden;
  touch-action: none;
  overflow-behavior: none;
}

a {
  color: inherit;
}

img {
  vertical-align: top;
}

ul, li {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: inherit;
  font-size: inherit;
}

input input[type=text],
input[type=email],
input[type=tel],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.input,
input {
  border-radius: 0;
  outline: none;
  display: block;
  width: 100%;
  border: 0 none;
}

textarea {
  resize: none;
  padding: 0 0;
}

[class*=__wrapper] {
  margin: 0 auto;
  padding-left: 25px;
  padding-right: 25px;
  max-width: 1954px;
}

.hidden {
  display: none !important;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  padding-bottom: 52.25%;
}

.ibg img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

._prod-img {
  position: relative;
  padding-bottom: 80%;
}
._prod-img img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

._fit {
  padding-bottom: 80%;
  position: relative;
}

._fit ._fit-img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  object-fit: cover;
  object-position: center center;
}

@supports (backdrop-filter: blur(10px)) or (--webkit-backdrop-filter: blur(10px)) {
  .bd-filter-fallback {
    background-color: rgba(255, 255, 255, 0.72);
    backdrop-filter: blur(31px);
  }
}

.form__label {
  display: block;
}
.form__label:not(:last-child) {
  margin-bottom: 1.5rem;
}
.form__label--error .form__error-text {
  display: block;
}

.form__label-inner {
  position: relative;
}

.form__input {
  height: 50px;
  padding-left: 16px;
  padding-right: 16px;
  border: 1px solid #000;
  padding-right: 55px;
  background: #fff;
  font-family: inherit;
}
.form__input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
  opacity: 1;
}
.form__input::-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
  opacity: 1;
}
.form__input:-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
  opacity: 1;
}
.form__input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
  opacity: 1;
}
.form__input--error {
  border-color: red;
}
.form__input--active {
  border: 2px solid rgb(1, 63, 125);
}
.form__input:focus, .form__input:not(:placeholder-shown) {
  padding-top: 1.125rem;
}
.form__input:focus ~ .form__placeholder, .form__input:not(:placeholder-shown) ~ .form__placeholder {
  top: 11px;
  transform: translateY(0);
  font-size: 12px;
}

.form__placeholder {
  position: absolute;
  top: 50%;
  left: 16px;
  transform: translateY(-50%);
  font-size: 14px;
  line-height: 1;
  color: #8E8E93;
  transition: all 0.2s;
  pointer-events: none;
}

.form__error-text {
  display: none;
  margin-top: 0.5rem;
  color: red;
  font-size: 0.875rem;
  line-height: 1;
}

.form__custom-policy {
  display: flex;
  align-items: center;
}

.form__custom {
  position: relative;
  flex-shrink: 0;
  margin-right: 33px;
}

.form__check {
  position: absolute;
  box-sizing: border-box;
  width: 1px;
  height: 1px;
  display: block;
  left: 50%;
  top: 100%;
  margin: 0;
  padding: 0;
  opacity: 0;
}
.form__check:checked + .form__custom-chek .icon-check {
  display: block;
}

.form__custom-chek {
  display: block;
  position: relative;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  background-color: #4AB8E6;
  border-radius: 7px;
}
@media (any-hover: hover) {
  .form__custom-chek:hover {
    cursor: pointer;
  }
}
.form__custom-chek .icon-check {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 14px;
  color: #fff;
  display: none;
}

.form__area {
  width: 100%;
  height: 100px;
  padding: 20px;
  resize: none;
  border: none;
  background: #fff;
  padding-left: 20px;
  color: #222222;
  font-family: inherit;
}

.form__policy {
  font-size: 20px;
  line-height: 1.35;
}
.form__policy a {
  font-family: "MontSemiBold", sans-serif;
}

.eye-ico {
  width: 24px;
  height: 24px;
  position: relative;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  font-family: inherit;
  position: absolute;
  bottom: 12px;
  right: 20px;
}
@media (any-hover: hover) {
  .eye-ico {
    cursor: pointer;
  }
}
.eye-ico .icon {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 14px;
}
.eye-ico.unblind .eye-ico__show {
  display: block;
}
.eye-ico.unblind .eye-ico__hide {
  display: none;
}
.eye-ico .eye-ico__show {
  display: none;
}

.inp-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  font-family: inherit;
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
}
@media (any-hover: hover) {
  .inp-reset {
    cursor: pointer;
  }
}

